import React, {Fragment, useState} from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { storeData, updateData, setStatus, getRow, setInput } from '../../../reduce/absensi/actions';
import { useForm } from 'react-hook-form';
import { config } from '../../../config';
import moment from 'moment';
import 'moment/locale/id';

import { rules } from './Validation';
import Toast from '../../atom/Toast';
import ContentLoaderRow from '../../atom/ContentLoaderRow'
import Button from '../../atom/Button';


const Form = () => {

    const { register, handleSubmit, setValue, setError, clearErrors, formState:{ errors } } = useForm();
    let dispatch = useDispatch();
    let response = useSelector( state => state.absensies );
	let redirect = useNavigate();
    let params = useParams();
    const [file, setFile] = useState('')

    React.useEffect(() => {
        if (params.id) {
            dispatch(setStatus('process'));
            let result = dispatch(getRow(params.id));
            result.then(function (data) {

                if(data.err)
                    return dispatch(setStatus('error', data.err.response.data.message));

                let row = data.data.data

                dispatch(setStatus('success'));
                dispatch(setInput(data.data.data));

                setValue("start_date", row.start_date);
                setValue("end_date", row.end_date);
            })
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [dispatch, params.id]);

    // const onImageUpload = (file) => {
    //     clearErrors()
    //     setFile(file);
    // }

    const submitHandler = data => {

        if(file === '' && !params.id)
            return setError('file', {message: 'Silahkan upload file absensi !'});

        dispatch(setStatus('process'));
        
        let result
        if (params.id) {
            let formData = file !== '' ? {...data, file: file} : data;
            dispatch(setInput(formData));
            result = dispatch(updateData());
        } else {
            let formData = file !== '' ? {...data, file: file} : data;
            dispatch(setInput(formData));
            result = dispatch(storeData());
        }

        result.then(function(row) {
            if (row.error) {
                dispatch(setStatus('error', row.error.message));
                const fields = row.error.response.data.message;
                Object.keys(fields).forEach((field) => {
                    setError(field, {message: fields[field]});
                });
            } else {
                dispatch(setStatus('success', row.result.data.message));
                dispatch(setInput(''));
                redirect(`/absensi`);
            }
        });
    }

    const fileHandler = data => {
        if(data)
            window.location.replace(`${config.api_host}/storage/${data}`);
    }

    const numberWithCommas = (number) => {
        if(number > 0)
            return number.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");

        return 0;
    }

    if (response.input !== '') {
        console.log(response.input.absensi_karyawan);
    }

    return (
        <Fragment>
            <div className='row'>
                <div className='col-md-12 grid-margin stretch-card'>
                    {response.status === 'error' && <Toast property={'error'} text={response.message}></Toast>} 
                    <div className="card" >
                        <div className="card-body">
                            <form onSubmit={handleSubmit(submitHandler)}>
                                { response.status === 'process' && response.input === '' ? <ContentLoaderRow row={6}></ContentLoaderRow> : <div className='row'>
                                    <div className='col-md-12'>
                                        
                                        <div className="container-fluid d-flex justify-content-between">
                                            <div className="col-lg-3 ps-0">
                                                <p>Validasi Upah Karyawan,<br/> PT.Logam Jaya,<br/>Cimahi.</p>
                                                <h5 className="mt-5 mb-2 text-muted">Invoice to :</h5>
                                                <p>Joseph E Carr,<br/> 102, 102  Crown Street,<br/> London, W3 3PR.</p>
                                            </div>
                                            <div className="col-lg-3 pe-0">
                                                <h4 className="fw-bolder text-uppercase text-end mt-4 mb-2">Absensi Karyawan</h4>
                                                <h6 className="text-end mb-5 pb-4"># INV-002308</h6>
                                                <p className="text-end mb-1">Periode Gaji</p>
                                                <h6 className="mb-0 mt-3 text-end fw-normal mb-2"><span className="text-muted">Awal :</span> {moment(response.input.start_date).format('LL')}</h6>
                                                <h6 className="text-end fw-normal"><span className="text-muted">Akhir :</span> {moment(response.input.end_date).format('LL')}</h6>
                                            </div>
                                        </div>

                                        <div className="table-responsive">
                                            <table className="table table-bordered">
                                                <thead>
                                                    <tr>
                                                        <th>No Absen</th>
                                                        <th>No Karyawan</th>
                                                        <th>Nama</th>
                                                        <th>Detail Gaji</th>
                                                        <th>Detail Absen</th>
                                                        <th>Start date</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {response.input !== '' && response.input.absensi_karyawan.map((karyawan, index) => 
                                                        <Fragment key={index}>
                                                            {karyawan.m_karyawan_id !== null && 
                                                                <tr>
                                                                    <td>{karyawan.no_absent}</td>
                                                                    <td>{karyawan.no_karyawan}</td>
                                                                    <td>{karyawan.absent_name}</td>
                                                                    <td style={{backgroundColor: "cornsilk"}}>
                                                                        <b >Gaji Pokok Harian</b> <span style={{float: "right"}} >{numberWithCommas(karyawan.total_regular_salary)}</span>
                                                                        <br></br>
                                                                        <span>Lembur Weekend</span> <span style={{float: "right"}} >{numberWithCommas(karyawan.total_overtime_salary)}</span>
                                                                        <hr></hr>
                                                                        <span>Absent</span> <span style={{float: "right"}} >{numberWithCommas(karyawan.absent_cut)}</span>
                                                                        <br></br>
                                                                        <span>BPJS Kesehatan</span> <span style={{float: "right"}} >{numberWithCommas(karyawan.bpjs_kesehatan_cut)}</span>
                                                                        <br></br>
                                                                        <span>BPJS Ketenagakerjaan</span> <span style={{float: "right"}} >{numberWithCommas(karyawan.bpjs_ketenagakerjaan_cut)}</span>
                                                                        <hr style={{height: "2px"}}></hr>
                                                                        <b style={{backgroundColor: "burlywood"}}>Take Home Pay</b> <span style={{float: "right", backgroundColor: "burlywood"}} >{ 
                                                                            'IDR ' + numberWithCommas((parseInt(karyawan.total_regular_salary) + parseInt(karyawan.total_overtime_salary)) - (parseInt(karyawan.absent_cut) + parseInt(karyawan.bpjs_kesehatan_cut) + parseInt(karyawan.bpjs_ketenagakerjaan_cut) ))
                                                                        }</span>
                                                                    </td>
                                                                    <td>June 21, 2021 {index}</td>
                                                                </tr> 
                                                            }
                                                        </Fragment>
                                                    )}
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div> }
                                { response.status === 'process' ?
                                    <button type="submit" disabled className="btn btn-primary">Loading&hellip;</button> : 
                                    <button type="submit" className="btn btn-primary">Submit</button> }
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
export default Form